import React, { Component } from 'react';




export default class App extends Component {
    state = {
        // todos: initialTodos,
        inputValue: "",
    };

    deleteTodo =todoId=> {
        this.setState(prevState => ({
          todos:prevState.todos.filter(todo=>todo.id!==todoId)
      }))
  }

    render() {
        
        return (
            
                  <input type="text" value={ this.state.inputValue}/>
            
        );

        }
}
